<template>
  <div class="container">
    <div class="buttons">
      <button class="button" :class="{ 'is-active': isBarActive_Income }" @click="drawBarChart_Income"
        style="margin-top: 50px;"></button>
      <button class="button" :class="{ 'is-active': isLineActive_Income }" @click="drawLineChart_Income"
        style="margin-top: 50px;"></button>
    </div>
    <div class="chart-container" id="income"></div>

    <!--  style="margin-top -->
    <div class="buttons">
      <button class="button" :class="{ 'is-active': isBarActive_Engel }" @click="drawBarChart_Engel"
        style="margin-top: 50px;"></button>
      <button class="button" :class="{ 'is-active': isLineActive_Engel }" @click="drawLineChart_Engel"
        style="margin-top: 50px;"></button>
    </div>
    <div class="chart-container" id="engelcoefficient"></div>

    <!--  style="margin-top -->
    <div class="buttons">
      <button class="button" :class="{ 'is-active': isBarActive_Gini }" @click="drawBarChart_Gini"
        style="margin-top: 50px;"></button>
      <button class="button" :class="{ 'is-active': isLineActive_Gini }" @click="drawLineChart_Gini"
        style="margin-top: 50px;"></button>
    </div>
    <div class="chart-container" id="ginicoefficient"></div>

  </div>
</template>

<script>

import { params_livingStandards, sendRequest, drawCommonChart } from './CommonUtil';
export default {

  data() {
    return {
      EChartType_LivingStandards: {
        IC: 'income',
        EC: 'engelcoefficient',
        GC: 'ginicoefficient'
      },
      isBarActive_Income: false,
      isLineActive_Income: false,
      isBarActive_Engel: false,
      isLineActive_Engel: false,
      isBarActive_Gini: false,
      isLineActive_Gini: false,
      returnData: null,
      sjList: null,
      chartType: null
    };
  },
  mounted() {
    this.loadData();
  },

  methods: {
    loadData() {
      if (process.env.VUE_APP_REQUEST_IS_LOCAL === 'true') {
        this.requestWithLocalJson()
      } else {
        this.requestWithAPI()
      }
    },
    requestWithLocalJson() {
      // 
      fetch('livingStandards.json')
        .then(response => response.json())
        .then(data => {
          console.log(':', data);
          // 
          this.returnData = data;
          // 
          this.drawChartWithBtn()
        })
        .catch(error => {
          console.error('Error fetching data:', error)
        })
    },
    async requestWithAPI() {
      try {
        this.returnData = await sendRequest(params_livingStandards);
        console.log("", this.returnData)
        this.drawChartWithBtn()
      } catch (error) {
        console.error(':', error);
      }
    },
    drawChartWithBtn() {
      if (this.returnData) {
        this.drawBarChart_Income()
        this.drawBarChart_Engel()
        this.drawBarChart_Gini()
      }
    },
    drawBarChart_Income() {
      this.isBarActive_Income = true;
      this.isLineActive_Income = false;
      this.chartType = "bar"
      this.drawChartWithParams({ echrtId: this.EChartType_LivingStandards.IC, title: '',exceptName: '', unit: '()' })
    },
    drawLineChart_Income() {
      this.isBarActive_Income = false;
      this.isLineActive_Income = true;
      // 
      this.chartType = "line"
      this.drawChartWithParams({ echrtId: this.EChartType_LivingStandards.IC, title: '',exceptName: '', unit: '()' })
    },
    drawBarChart_Engel() {
      this.isBarActive_Engel = true;
      this.isLineActive_Engel = false;
      this.chartType = "bar"
      const subtitle = ' \n 59% 50~59% \n 40~50% 30~40% \n 20~30% 20%'
      this.drawChartWithParams({ echrtId: this.EChartType_LivingStandards.EC, title: '', subtitle: subtitle})
    },
    drawLineChart_Engel() {
      this.isBarActive_Engel = false;
      this.isLineActive_Engel = true;
      // 
      this.chartType = "line"
      const subtitle = ' \n 59% 50~59% \n 40~50% 30~40% \n 20~30% 20%'
      this.drawChartWithParams({ echrtId: this.EChartType_LivingStandards.EC, title: '', subtitle: subtitle})
    },
    drawBarChart_Gini() {
      this.isBarActive_Gini = true;
      this.isLineActive_Gini = false;
      this.chartType = "bar"
      const subtitle = ' \n 0.2-0.29() \n 0.3-0.39() \n 0.4-0.59() \n 0.6() \n 0.4'
      this.drawChartWithParams({ echrtId: this.EChartType_LivingStandards.GC, title: '', subtitle: subtitle})
    },
    drawLineChart_Gini() {
      this.isBarActive_Gini = false;
      this.isLineActive_Gini = true;
      // 
      this.chartType = "line"
      const subtitle = ' \n 0.2-0.29() \n 0.3-0.39() \n 0.4-0.59() \n 0.6() \n 0.4'
      this.drawChartWithParams({ echrtId: this.EChartType_LivingStandards.GC, title: '', subtitle: subtitle})
    },
    drawChartWithParams({ echrtId, title, exceptName = '', unit = '', subtitle = '', sj = '0' } = {}) {
      // basicParams-echrtIdtitlelegendTopgridTopxAxisDataArr
      let basicParams = {};
      let typeArr = [];
      switch (echrtId) {
        case this.EChartType_LivingStandards.IC:
          basicParams = { echrtId: echrtId, chartType: this.chartType, title: title, subtitle: subtitle, exceptName: exceptName, unit: unit, legendTop: '10%', gridTop: '30%', sj: sj }
          // A0A0101- A0A0103- A0A0201-   
          // A0A0203- A0A0301- A0A0303-
          typeArr = ['A0A0101', 'A0A0103', 'A0A0201','A0A0203', 'A0A0301', 'A0A0303'];
          break;
        case this.EChartType_LivingStandards.EC:
          basicParams = { echrtId: echrtId, chartType: this.chartType, title: title, subtitle: subtitle, exceptName: exceptName, unit: unit, legendTop: '20%', gridTop: '35%', sj: sj, min: '25', max: '50' }
          // A0A0H01- A0A0H02- A0A0H03-  
          typeArr = ['A0A0H01', 'A0A0H02', 'A0A0H03'];
          break;
        case this.EChartType_LivingStandards.GC:
          basicParams = { echrtId: echrtId, chartType: this.chartType, title: title, subtitle: subtitle, exceptName: exceptName, unit: unit, legendTop: '27%', gridTop: '35%', sj: sj, min: '0.4', max: '0.5' }
          // A0A0G01-
          typeArr = ['A0A0G01'];
          break;
        default:
          break;
      }
      drawCommonChart(basicParams, typeArr, this.returnData)
    }
  }
};
</script>