  <template>
    <div class="container">
      <div class="buttons">
        <button class="button" :class="{ 'is-active': isBarActive_Indices }" @click="drawBarChart_Indices"
          style="margin-top: 50px;"></button>
        <button class="button" :class="{ 'is-active': isLineActive_Indices }" @click="drawLineChart_Indices"
          style="margin-top: 50px;"></button>
      </div>

      <div class="chart-container" id="priceIndices"></div>

      <!--  style="margin-top -->
      <div class="buttons">
        <button class="button" :class="{ 'is-active': isBarActive_PMI }" @click="drawBarChart_PMI"
          style="margin-top: 50px;"></button>
        <button class="button" :class="{ 'is-active': isLineActive_PMI }" @click="drawLineChart_PMI"
          style="margin-top: 50px;"></button>
      </div>
      <div class="chart-container" id="pmi"></div>
    </div>
  </template>

<script>
import * as echarts from 'echarts';
import { params_indices, sendRequest, selectDataFromArr } from './CommonUtil';
export default {

  data() {
    return {
      IndicesType: {
        CPI_A01010101: 'A01010101', //  CPI 
        CPI_A01030101: 'A01030101', //  CPI 
        PPI_A01080101: 'A01080101', //  PPI 
        PPI_A01080701: 'A01080701', //  PPI 
        PMI_A0B0101: 'A0B0101',   //  
        PMI_A0B0201: 'A0B0201',   //  
        PMI_A0B0301: 'A0B0301'    //  
      },
      isBarActive_Indices: false,
      isLineActive_Indices: false,
      isBarActive_PMI: false,
      isLineActive_PMI: false,
      returnData: null,
      chartsType: null
    };
  },
  mounted() {
    this.loadData();
  },

  methods: {
    loadData() {
      if (process.env.VUE_APP_REQUEST_IS_LOCAL === 'true') {
        this.requestWithLocalJson()
      } else {
        this.requestWithAPI()
      }
    },
    requestWithLocalJson() {
      // 
      fetch('indices.json')
        .then(response => response.json())
        .then(data => {
          console.log(':', data);
          // 
          this.returnData = data;
          // 
          this.drawBarChart_Indices();
          this.drawBarChart_PMI();
        })
        .catch(error => {
          console.error('Error fetching data:', error)
        })
    },
    async requestWithAPI() {
      try {
        this.returnData = await sendRequest(params_indices);
        console.log("", this.returnData)
        if (this.returnData) {
          this.drawBarChart_Indices();
          this.drawBarChart_PMI();
        }
      } catch (error) {
        console.error(':', error);
      }
    },

    // cpi
    drawPriceIndicesChat() {
      // domecharts
      var priceIndicesChart = echarts.init(document.getElementById('priceIndices'));
      // 
      var priceIndicesOption = {
        title: {
          text: '',
          subtext: 'CPI \n  \n PPI \n  \n  ',
          left: 'center',
          top: 'top',

          subtextStyle: {
            fontWeight: 'bold',
            fontSize: 13,
            lineHeight: 20,
          }
        },
        tooltip: {
          //X
          trigger: 'axis'
        },
        legend: {
          left: 'center',
          top: '25%'
        },
        grid: {
          left: '1%',
          right: '1%',
          top: '40%',
          bottom: '1%',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          data: this.returnData.sj[0].sort()
        },
        yAxis: {
          min: '94', // 00
          max: '101'
        },
        series: [
          {
            name: 'CPI(=100)',
            type: this.chartsType,
            data: selectDataFromArr(this.returnData, this.IndicesType.CPI_A01010101)
          },
          {
            name: 'CPI(=100)',
            type: this.chartsType,
            data: selectDataFromArr(this.returnData, this.IndicesType.CPI_A01030101)
          },
          {
            name: 'PPI(=100)',
            type: this.chartsType,
            data: selectDataFromArr(this.returnData, this.IndicesType.PPI_A01080101)
          },
          {
            name: 'PPI(=100)',
            type: this.chartsType,
            data: selectDataFromArr(this.returnData, this.IndicesType.PPI_A01080701)
          }
        ]
      };
      // 
      priceIndicesChart.setOption(priceIndicesOption);
    },

    // pmi
    drawPMIChart() {
      // domecharts
      var pmiChart = echarts.init(document.getElementById('pmi'));
      // 
      var pmiOption = {
        title: {
          text: '',
          subtext: 'PMI \n PMI50% \n PMI50, \n PMI50',
          left: 'center',
          top: 'top',
          subtextStyle: {
            fontWeight: 'bold',
            fontSize: 13,
            lineHeight: 20,
          }
        },
        tooltip: {
          //X
          trigger: 'axis'
        },
        legend: {
          left: 'center',
          top: '20%'
        },
        grid: {
          left: '1%',
          right: '1%',
          top: '35%',
          bottom: '1%',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          data: this.returnData.sj[0].sort()
        },
        yAxis: {
          min: '48',
          max: '57'
        },
        series: [
          {
            name: 'PMI',
            type: this.chartsType,
            data: selectDataFromArr(this.returnData, this.IndicesType.PMI_A0B0101)
          },
          {
            name: 'PMI',
            type: this.chartsType,
            data: selectDataFromArr(this.returnData, this.IndicesType.PMI_A0B0201)
          },
          {
            name: 'PMI',
            type: this.chartsType,
            data: selectDataFromArr(this.returnData, this.IndicesType.PMI_A0B0301)
          }
        ]
      };
      // 
      pmiChart.setOption(pmiOption);
    },

    drawBarChart_Indices() {
      this.isBarActive_Indices = true;
      this.isLineActive_Indices = false;
      // 
      this.chartsType = "bar"
      this.drawPriceIndicesChat();

    },
    drawLineChart_Indices() {
      this.isBarActive_Indices = false;
      this.isLineActive_Indices = true;
      // 
      this.chartsType = "line"
      this.drawPriceIndicesChat();
    },
    drawBarChart_PMI() {
      this.isBarActive_PMI = true;
      this.isLineActive_PMI = false;
      // 
      this.chartsType = "bar"
      this.drawPMIChart();

    },
    drawLineChart_PMI() {
      this.isBarActive_PMI = false;
      this.isLineActive_PMI = true;
      // 
      this.chartsType = "line"
      this.drawPMIChart()
    }
  }
};
</script>


