<template>
    <div class="container">
        <div class="buttons">
            <button class="button" :class="{ 'is-active': isBarActive_Agency }" @click="drawBarChart_Agency"
                style="margin-top: 50px;"></button>
            <button class="button" :class="{ 'is-active': isLineActive_Agency }" @click="drawLineChart_Agency"
                style="margin-top: 50px;"></button>
        </div>
        <div class="chart-container" id="agency"></div>

        <!--  style="margin-top -->
        <div class="buttons">
            <button class="button" :class="{ 'is-active': isBarActive_Officer }" @click="drawBarChart_Officer"
                style="margin-top: 50px;"></button>
            <button class="button" :class="{ 'is-active': isLineActive_Officer }" @click="drawLineChart_Officer"
                style="margin-top: 50px;"></button>
        </div>
        <div class="chart-container" id="officer"></div>

        <!--  style="margin-top -->
        <div class="buttons">
            <button class="button" :class="{ 'is-active': isBarActive_Bed }" @click="drawBarChart_Bed"
                style="margin-top: 50px;"></button>
            <button class="button" :class="{ 'is-active': isLineActive_Bed }" @click="drawLineChart_Bed"
                style="margin-top: 50px;"></button>
        </div>
        <div class="chart-container" id="bed"></div>

    </div>
</template>

<script>
import * as echarts from 'echarts';
import { params_medical, sendRequest, selectDataFromArr } from './CommonUtil';
export default {

    data() {
        return {
            Medical: {
                // 
                A0O0101: 'A0O0101',   // 
                A0O0102: 'A0O0102',   // 
                A0O0106: 'A0O0106',   // 
                // 
                A0O0201: 'A0O0201',   // 
                A0O0202: 'A0O0202',   // 
                A0O0204: 'A0O0204',   // 
                A0O0205: 'A0O0205',   // 
                // 
                A0O0501: 'A0O0501',   // 
                A0O0502: 'A0O0502',   // 
                A0O0506: 'A0O0506',   // 

            },

            isBarActive_Agency: false,
            isLineActive_Agency: false,
            isBarActive_Officer: false,
            isLineActive_Officer: false,
            isBarActive_Bed: false,
            isLineActive_Bed: false,
            returnData: null,
            sjList: null,
            chartsType: null
        };
    },
    mounted() {
        this.loadData();
    },

    methods: {
        loadData() {
            if (process.env.VUE_APP_REQUEST_IS_LOCAL === 'true') {
                this.requestWithLocalJson()
            } else {
                this.requestWithAPI()
            }
        },
        requestWithLocalJson() {
            // 
            fetch('medical.json')
                .then(response => response.json())
                .then(data => {
                    console.log(':', data);
                    // 
                    this.returnData = data;
                    // 
                    this.drawBarChart_Agency()
                    this.drawBarChart_Officer()
                    this.drawBarChart_Bed()
                })
                .catch(error => {
                    console.error('Error fetching data:', error)
                })
        },
        async requestWithAPI() {
            try {
                this.returnData = await sendRequest(params_medical);
                console.log("", this.returnData)
                if (this.returnData) {
                    this.drawBarChart_Agency()
                    this.drawBarChart_Officer()
                    this.drawBarChart_Bed()
                }
            } catch (error) {
                console.error(':', error);
            }
        },
        // 
        drawAgencyCharts() {
            // domecharts
            var agencyChart = echarts.init(document.getElementById('agency'));
            // 
            var agencyOption = {
                title: {
                    text: '',
                    left: 'center',
                    top: 'top'
                },
                tooltip: {
                    //X
                    trigger: 'axis'
                },
                legend: {
                    left: 'center',
                    top: '10%'
                },
                grid: {
                    left: '1%',
                    right: '1%',
                    top: '20%',
                    bottom: '1%',
                    containLabel: true
                },
                xAxis: {
                    type: 'category',
                    data: this.returnData.sj[0].sort()
                },
                yAxis: {
                },

                series: [
                    {
                        name: '()',
                        type: this.chartsType,
                        data: selectDataFromArr(this.returnData, this.Medical.A0O0101)
                    },
                    {
                        name: '()',
                        type: this.chartsType,
                        data: selectDataFromArr(this.returnData, this.Medical.A0O0102)
                    },
                    {
                        name: '()',
                        type: this.chartsType,
                        data: selectDataFromArr(this.returnData, this.Medical.A0O0106)
                    }

                ]
            };
            // 
            agencyChart.setOption(agencyOption);
        },
        // 
        drawOfficerCharts() {
            // domecharts
            var officerChart = echarts.init(document.getElementById('officer'));
            // 
            var officerOption = {
                title: {
                    text: '',
                    left: 'center',
                    top: 'top',
                },
                tooltip: {
                    //X
                    trigger: 'axis'
                },
                legend: {
                    left: 'center',
                    top: '10%'
                },
                grid: {
                    left: '1%',
                    right: '1%',
                    top: '20%',
                    bottom: '1%',
                    containLabel: true
                },
                xAxis: {
                    type: 'category',
                    data: this.returnData.sj[0].sort()
                },
                yAxis: {

                },

                series: [
                    {
                        name: '()',
                        type: this.chartsType,
                        data: selectDataFromArr(this.returnData, this.Medical.A0O0201)
                    },
                    {
                        name: '()',
                        type: this.chartsType,
                        data: selectDataFromArr(this.returnData, this.Medical.A0O0202)
                    },
                    {
                        name: '()',
                        type: this.chartsType,
                        data: selectDataFromArr(this.returnData, this.Medical.A0O0204)
                    },
                    {
                        name: '()',
                        type: this.chartsType,
                        data: selectDataFromArr(this.returnData, this.Medical.A0O0205)
                    }

                ]
            };
            // 
            officerChart.setOption(officerOption);
        },
        // 
        drawBedCharts() {
            // domecharts
            var bedChart = echarts.init(document.getElementById('bed'));
            // 
            var bedOption = {
                title: {
                    text: '',
                    left: 'center',
                    top: 'top',
                },
                tooltip: {
                    //X
                    trigger: 'axis'
                },
                legend: {
                    left: 'center',
                    top: '10%'
                },
                grid: {
                    left: '1%',
                    right: '1%',
                    top: '20%',
                    bottom: '1%',
                    containLabel: true
                },
                xAxis: {
                    type: 'category',
                    data: this.returnData.sj[0].sort()
                },
                yAxis: {

                },
                series: [
                    {
                        name: '()',
                        type: this.chartsType,
                        data: selectDataFromArr(this.returnData, this.Medical.A0O0501)
                    },
                    {
                        name: '()',
                        type: this.chartsType,
                        data: selectDataFromArr(this.returnData, this.Medical.A0O0502)
                    },
                    {
                        name: '()',
                        type: this.chartsType,
                        data: selectDataFromArr(this.returnData, this.Medical.A0O0506)
                    }
                ]
            };
            // 
            bedChart.setOption(bedOption);
        },
        drawBarChart_Agency() {
            this.isBarActive_Agency = true;
            this.isLineActive_Agency = false;
            this.chartsType = "bar"
            this.drawAgencyCharts()

        },
        drawLineChart_Agency() {
            this.isBarActive_Agency = false;
            this.isLineActive_Agency = true;
            // 
            this.chartsType = "line"
            this.drawAgencyCharts()
        },
        drawBarChart_Officer() {
            this.isBarActive_Officer = true;
            this.isLineActive_Officer = false;
            this.chartsType = "bar"
            this.drawOfficerCharts()

        },
        drawLineChart_Officer() {
            this.isBarActive_Officer = false;
            this.isLineActive_Officer = true;
            // 
            this.chartsType = "line"
            this.drawOfficerCharts()
        },
        drawBarChart_Bed() {
            this.isBarActive_Bed = true;
            this.isLineActive_Bed = false;
            this.chartsType = "bar"
            this.drawBedCharts()

        },
        drawLineChart_Bed() {
            this.isBarActive_Bed = false;
            this.isLineActive_Bed = true;
            // 
            this.chartsType = "line"
            this.drawBedCharts()
        }

    }
};
</script>